@import 'variables';

.card {
  padding: 32px 16px 16px;
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  transition: transform 300ms ease;

  &:hover {
    transform: scale(1.2);
  }

  &__background_image {
    display: flex;
    width: 160px;
    height: 134px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    border: 0;
    margin: 0 auto;
  }

  &__text {
    display: flex;
    font-size: 12px;
    font-weight: 500;
    margin-top: 40px;
    margin-bottom: 0;
    line-height: 18px;
    color: $black-color;
    transition: color 300ms ease;
  }

  &__product_code {
    display: flex;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    margin-top: 4px;
    color: $grey-color;
  }

  &__price,
  &__amount {
    display: inline;
  }

  &__price_list {
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
  }

  &__price {
    margin-top: 0;
    display: flex;
    line-height: 18px;
    font-size: 12px;
    color: $grey-color;
    transition: transform 300ms ease;
  }

  &__amount {
    margin-top: 0;
    font-weight: 700;
    color: $black-color;
    line-height: 18px;
    font-size: 16px;
  }

  &__buy_button {
    display: flex;
    width: 100%;
    height: 40px;
    justify-content: center;
    align-items: center;
    background-color: $blue-color;
    color: $white-color;
    border-radius: 5px;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    border: none;
    cursor: pointer;
    position: relative;

    &:hover {
      background-color: $white-color;
      color: $blue-color;
      border-color: $blue-color;
      transition:
        background-color 300ms ease,
        color 300ms ease,
        border-color 300ms ease;
    }
  }
}

.card:hover .card__text {
  color: #34568b;
}
