.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  align-items: center;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  text-align: center;
  height: 60px;
  margin-right: 20px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
  height: 60px;
  line-height: 60px;
  text-decoration-color: #00acdc;
  text-decoration-thickness: 2px;
  transition: color .3s;
}

.nav__list {
  margin: 0;
  padding-inline-start: 0;
  list-style: none;
  display: flex;
}

.nav__list li:last-child a {
  margin-right: 0;
}

.is-active {
  color: #00acdc;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.is-active .nav__link {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__background_image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border: 0;
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: flex;
}

.card__text {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
  display: flex;
}

.card__product_code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price, .card__amount {
  display: inline;
}

.card__price_list {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price {
  color: #616070;
  margin-top: 0;
  font-size: 12px;
  line-height: 18px;
  transition: transform .3s;
  display: flex;
}

.card__amount {
  color: #060b35;
  margin-top: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy_button {
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
  position: relative;
}

.card__buy_button:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
  transition: background-color .3s, color .3s, border-color .3s;
}

.card:hover .card__text {
  color: #34568b;
}

.starsBox {
  z-index: 2;
  flex-direction: row;
  justify-content: space-between;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.reviews {
  text-align: right;
  color: #060b35;
  height: 16px;
  margin: 0;
  padding-top: 2px;
  padding-bottom: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:first-child, .stars__star:nth-child(2), .stars__star:nth-child(3), .stars__star:nth-child(4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars--0, .stars--1, .stars--2, .stars--3, .stars--5 {
  display: none;
}

.wrapper {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px;
  display: grid;
}

@media screen and (width >= 488px) {
  .wrapper {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) {
  .wrapper {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .wrapper {
    grid-template-columns: repeat(4, 200px);
  }
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}
